.search-container{
    display: flex;
    justify-content: center;
    margin-top:40px;
}

.search-input{
    padding: 8px 15px;
    font-size: 20px;
    width:250px;
    border-width: 0;
    background: #a592f2;
    color:#e7e9f0;
    border-radius: 40px;
    box-shadow: 0 0 5px rgba(255,255,255,0.9);
}

.search-input::placeholder{
    color:#e7e9f0;
}
 .search-input:focus{
     outline: none;
 }